.clock {
  width: 120px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: solid 1px #ccc;
  border-radius: 5px;
  margin: 5px;
  background: #ccc;
  .digital-clock {
    padding: 10px;
  }
  .analog-clock {
    width: 100px;
    height: 100px;
    border: solid 2px #000;
    border-radius: 50%;
    position: relative;
    background: #fff;
    .dial {
      position: absolute;
      left: 50%;
      width: 50px;
      height: 50px;
      transform-origin: bottom left;
      &.seconds {
        border-left:solid 1px green;
      }
      &.minutes {
        border-left:solid 1px #cc0000;
      }
      &.hours {
        width: 35px;
        height: 35px;
        top: 15px;
        border-left:solid 1px #000;
      }
    }
    //.dial::before{
    //  width: 2px;
    //  content: '';
    //  height: 2px;
    //  background: black;
    //  display: block;
    //  /* left: 120px; */
    //  border: 1px solid;
    //  border-radius: 2px;
    //  position: relative;
    //  left: -3px;
    //}
  }
}
.centralDot {
  position: absolute;
  width: 5px;
  height: 5px;
  background: black;
  border-radius: 50%;
  top: 48%;
  left: 48%;
}